"use client";

import React from "react";
import { useRouter } from "next/router";
import Button from "@/src/component/ui/button/button";

const ErrorPage = () => {
  const router = useRouter();

  const goBack = () => {
    router.push("/technology-selection");
  };

  return (
    <>
      <div className=" flex flex-col items-center justify-center min-h-screen text-center">
        <h1 className="text-3xl font-poppins sm:text-3xl text-red-700">
          Error
        </h1>
        <p className="mt-4 mb-4 text-lg text-red-600 font-sans text-base">
          An error occurred. Please try again later.
        </p>
        <Button description="Try again" onClick={goBack} />{" "}
      </div>
    </>
  );
};

export default ErrorPage;
